* {
   box-sizing: border-box;
}

body {
   font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
      Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
   -webkit-font-smoothing: antialiased;
   -moz-osx-font-smoothing: grayscale;
   background-color: #fafafa;
   color: #212121;
   line-height: 1.5;
}

#search-box {
   font: inherit;
   padding: 8px;
   max-width: 320px;
   margin-bottom: 16px;
}

.country-image {
   width: 30px;
   margin-right: 10px;
}

.country-list {
   padding: 0px;
   list-style: none;
   font-weight: 500;
   font-size: 20px;
}

.country-list span {
   font-size: 30px;
   font-weight: 700;
}

.country-item {
   display: flex;
   align-items: center;
}

p span {
   font-size: 22px;
   font-weight: 700;
}

.country-info {
   font-size: 18px;
}